"use client";

import { useState } from "react";

export default function AddRequestPage() {
  const [formData, setFormData] = useState({
    bloodType: "O+",
    unitsNeeded: "",
    hospital: "",
    urgency: "LOW",
    locationKm: "",
  });

  const handleChange = (e: any) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: any) => {
    e.preventDefault();

    const response = await fetch("http://localhost:5000/request/add", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(formData),
    });

    const result = await response.json();

    if (result.success) {
      alert("Request Added Successfully!");
    } else {
      alert("Error: " + result.message);
    }
  };

  return (
    <div className="p-6 max-w-xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Add Blood Request</h1>

      <form onSubmit={handleSubmit} className="space-y-5">

        <div>
          <label className="block font-semibold mb-1">Blood Type</label>
          <select
            name="bloodType"
            className="w-full p-2 border rounded"
            value={formData.bloodType}
            onChange={handleChange}
          >
            {["O+", "O-", "A+", "A-", "B+", "B-", "AB+", "AB-"].map((bt) => (
              <option key={bt} value={bt}>
                {bt}
              </option>
            ))}
          </select>
        </div>

        <div>
          <label className="block font-semibold mb-1">Units Needed</label>
          <input
            type="number"
            name="unitsNeeded"
            className="w-full p-2 border rounded"
            value={formData.unitsNeeded}
            onChange={handleChange}
            required
          />
        </div>

        <div>
          <label className="block font-semibold mb-1">Hospital Name</label>
          <input
            type="text"
            name="hospital"
            className="w-full p-2 border rounded"
            value={formData.hospital}
            onChange={handleChange}
            required
          />
        </div>

        <div>
          <label className="block font-semibold mb-1">Urgency Level</label>
          <select
            name="urgency"
            className="w-full p-2 border rounded"
            value={formData.urgency}
            onChange={handleChange}
          >
            <option value="LOW">Low</option>
            <option value="MODERATE">Moderate</option>
            <option value="HIGH">High</option>
            <option value="CRITICAL">Critical</option>
          </select>
        </div>

        <div>
          <label className="block font-semibold mb-1">Distance (Km)</label>
          <input
            type="number"
            name="locationKm"
            className="w-full p-2 border rounded"
            value={formData.locationKm}
            onChange={handleChange}
            required
          />
        </div>

        <button
          type="submit"
          className="w-full bg-red-600 text-white py-3 rounded font-bold"
        >
          Submit Request
        </button>
      </form>
    </div>
  );
}
